<template>
  <div class="px-2 py-0">
    <content-placeholders class="mt-3" v-if="loading">
      <content-placeholders-text :lines="10" />
    </content-placeholders>
    <div v-else>
      <v-alert v-if="employeeList.length === 0" :value="true" outline color="info" icon="info">
        Không có dữ liệu.
      </v-alert>
      <v-card>
        <v-layout wrap class="px-0 py-0">
          <div style="width: calc(100% - 150px)">
            <v-layout wrap>
              <v-flex xs4 class="pl-2 pr-2">
                <v-text-field
                  label="Tên cán bộ"
                  placeholder="Nhấn để nhập tên cán bộ"
                  v-model="employeeName"
                  clearable
                ></v-text-field>
              </v-flex>
              <v-flex xs4 class="pl-2 pr-2">
                <v-select
                  :items="listCoQuan"
                  v-model="govAgencyKey"
                  autocomplete
                  label="Cơ quan"
                  placeholder="Chọn cơ quan"
                  item-text="govagencyName"
                  item-value="govagencyCode"
                  :hide-selected="true"
                  clearable
                ></v-select>
              </v-flex>
              <v-flex xs4 class="pl-2 pr-2">
                <v-select
                  :items="listDonVi"
                  v-model="govAgencyKey"
                  autocomplete
                  label="Đơn vị"
                  placeholder="Chọn đơn vị"
                  item-text="govagencyName"
                  item-value="govagencyCode"
                  :hide-selected="true"
                  clearable
                ></v-select>
              </v-flex>
            </v-layout>
          </div>
          <div class="pt-2 text-center" style="width: 150px">
            <v-btn color="primary"
              :loading="loadingTable"
              :disabled="loadingTable"
              @click="filterDossier"
            >
              <v-icon size="18">search</v-icon>
              &nbsp;
              Tra Cứu
              <span slot="loader">Loading...</span>
            </v-btn>
          </div>
        </v-layout>
        <v-layout wrap>
          <v-flex xs4 class="pl-2 pr-2" v-for="(item, index) in employeeList" :key="index">
            <v-card>
              <v-list two-line>
                <v-list-tile :key="item.title" avatar>
                  <v-list-tile-avatar>
                    <img v-if="item.photoFileEntryId" :src="item.photoFileEntryId">
                    <img v-else src="/o/frontend-web-kios/images/default_avatar.png" >
                  </v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title v-html="item.fullName"></v-list-tile-title>
                    <v-list-tile-title></v-list-tile-title>
                  </v-list-tile-content>
                </v-list-tile>
              </v-list>
              <p class="mb-2"><span>Ngày sinh:</span> {{item.birthdate | dateTimeView}}</p>
              <p class="mb-2"><span>Chức vụ :</span> {{item.jobTitle}}</p>
              <p class="mb-2"><span>Đơn vị:</span> {{item.workingUnitName}}</p>
              <v-btn color="primary" flat="flat"
              >
                <v-icon>star_rate</v-icon>&nbsp;
                Đánh giá
              </v-btn>
            </v-card>
          </v-flex>
        </v-layout>
      </v-card>
    </div>
  </div>
</template>

<script>
import router from '@/router'
import Vue from 'vue/dist/vue.min.js'
export default {
  props: [],
  components: {},
  data: () => ({
    loading: false,
    loadingAction: false,
    employeeList: []
  }),
  computed: {},
  created () {
    let vm = this
    vm.$nextTick(function () {
      var vm = this
      vm.loading = true
      let filter
      vm.$store.dispatch('getEmployee', filter).then(function (result) {
        // console.log('employeesCreated', result)
        setTimeout(function () {
          vm.loading = false
        }, 200)
        vm.employeeList = result
        let query = vm.$router.history.current.query
        if (vm.employeeList && vm.employeeList.length > 0) {
          for (let key in vm.employeeList) {
            vm.getEvaluationItem(key)
          }
        }
      }).catch(function (reject) {
        vm.loading = false
        vm.employeeList = []
      })
    })
  },
  watch: {
    '$route': function (newRoute, oldRoute) {
      let vm = this
      let currentParams = newRoute.params
      let currentQuery = newRoute.query
      vm.dossierNoKey = currentQuery.hasOwnProperty('dossierNo') ? currentQuery.dossierNo : ''
      vm.applicantIdNoKey = currentQuery.hasOwnProperty('applicantIdNo') ? currentQuery.applicantIdNo : ''
      vm.applicantNameKey = currentQuery.hasOwnProperty('applicantName') ? currentQuery.applicantName : ''
      vm.hosoDatasPage = 1
      vm.doLoadingDataHoSo()
    }
  },
  methods: {
    filterDossier () {
      var vm = this
      let current = vm.$router.history.current
      let newQuery = current.query
      let queryString = '?'
      newQuery['dossierNo'] = vm.dossierNoKey ? vm.dossierNoKey : ''
      newQuery['applicantIdNo'] = vm.applicantIdNoKey ? vm.applicantIdNoKey : ''
      newQuery['applicantName'] = vm.applicantNameKey ? vm.applicantNameKey : ''
      for (let key in newQuery) {
        if (newQuery[key] !== '' && newQuery[key] !== 'undefined' && newQuery[key] !== undefined && newQuery[key] !== null) {
          queryString += key + '=' + newQuery[key] + '&'
        }
      }
      vm.$router.push({
        path: current.path + queryString,
        query: {
          renew: Math.floor(Math.random() * (100 - 1 + 1)) + 1
        }
      })
    },
    showMore () {
      var vm = this
      vm.hosoDatasPage += 1
      vm.loadingTable = true
      let currentQuery = router.history.current.query
      var filter = null
      filter = {
        page: vm.hosoDatasPage,
        dossierNo: currentQuery.hasOwnProperty('dossierNo') ? currentQuery.dossierNo : '',
        applicantName: currentQuery.hasOwnProperty('applicantName') ? currentQuery.applicantName : '',
        applicantIdNo: currentQuery.hasOwnProperty('applicantIdNo') ? currentQuery.applicantIdNo : ''
      }
      vm.$store.dispatch('loadingDataHoSo', filter).then(function (result) {
        vm.loadingTable = false
        let dossierMore = result.data
        vm.dossierList = vm.dossierList.concat(dossierMore)
      }).catch(reject => {
        vm.loadingTable = false
      })
    },
    doLoadingDataHoSo () {
      let vm = this
      vm.dossierList = []
      vm.loadingTable = true
      let currentQuery = router.history.current.query
      var filter = null
      filter = {
        page: vm.hosoDatasPage,
        dossierNo: currentQuery.hasOwnProperty('dossierNo') ? currentQuery.dossierNo : '',
        applicantName: currentQuery.hasOwnProperty('applicantName') ? currentQuery.applicantName : '',
        applicantIdNo: currentQuery.hasOwnProperty('applicantIdNo') ? currentQuery.applicantIdNo : ''
      }
      vm.$store.dispatch('loadingDataHoSo', filter).then(function (result) {
        vm.loadingTable = false
        vm.dossierList = result.data
        vm.dossierItemTotal = result.total
        vm.totalPages = Math.ceil(result.total / 15)
      }).catch(reject => {
        vm.loadingTable = false
        vm.dossierList = []
        vm.dossierItemTotal = 0
      })
    },
    viewDetail (item) {
      router.push('/tra-cuu-ho-so/' + item.dossierId)
    }
  },
  filters: {
    dateTimeView (arg) {
      if (arg) {
        let value = new Date(arg)
        return `${value.getDate().toString().padStart(2, '0')}/${(value.getMonth() + 1).toString().padStart(2, '0')}/${value.getFullYear()}`
      } else {
        return ''
      }
    }
  }
}
</script>



